* {
  padding: 0;
  font-weight: 600;

  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  background-image: linear-gradient(to right bottom, #005aa7, #fffde4);
  background-attachment: fixed;
  color: #354538;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  background: white; 
  border-radius: 30px; 
  max-width: 500px;
  width: 100%;
  min-height: 400px;
  box-shadow: 0 2px 6px #555;
  margin: 50px auto;
  padding: 50px;
}

h1 {
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-weight: 900;
  color: #666;
}

form {
  width: 100%;
  height: 40px;
  margin: 20px 0;
  display: flex;
}

form input, form button {
  flex: 3;
  border: none;
  outline: none;
  border-bottom: 1px solid #555;
  margin: 0 5px;
}

form button {
  flex: 1;
  background: #555;
  color: white;
  letter-spacing: 2px;
  cursor: pointer;
}

ul li {
  list-style: none;
  border: 1px solid #ccc;
  margin: 10px 0;
  padding: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-transform: capitalize;
  border-radius: 5px;
}

ul .active {
  background-color: rgb(213, 217, 221);
}


ul li .active {
  opacity: 0.2;
  text-decoration: line-through;
}



ul li button {
 border: none;
 outline: none;
 color: white;
 background-image: linear-gradient(to right bottom, #005aa7, #fffde4);
 border-radius: 5px;
 padding: 5px 15px;
 cursor: pointer;
 margin-right: 5px;
}


ul li .Edit:disabled {
  opacity: 0.2;
  cursor: not-allowed;
}


ul li .Delete{
  background-image: linear-gradient(to right bottom, #fffde4, #dd7e46);
}

.row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 20px;
}

.row button {
  border: none;
 outline: none;
 color: white;
 background: #dd7e46;
 border-radius: 5px;
 padding: 5px 15px;
 cursor: pointer;
 margin-right: 5px;
}

label {
  cursor: pointer;
}


@media (max-width:400px) {
  .App {
    padding: 10px;
  }
}
